@charset "UTF-8";
.testimonialsListWrapper {
  position: relative;
  display: flex;
  overflow-x: hidden;
  box-sizing: border-box;
  margin: 10px;
}

.testimonialsList {
  display: flex;
  flex-direction: row;
  position: relative;
  left: 0;
  transition: left 0.3s;
}

.floatingArrow {
  position: absolute;
  top: 0;
  height: 100%;
  width: 40px;
  font-size: 80px;
  vertical-align: middle;
  overflow: hidden;
  opacity: 0.5;
  transition: opacity 0.5s;
  border-radius: 8px;
  cursor: pointer;
}
.floatingArrow.disabled {
  display: none;
}
.floatingArrow.left {
  left: 0;
  background: rgba(150, 150, 150, 0.8);
  -webkit-mask-image: -webkit-gradient(linear, center top, right top, from(rgba(255, 0, 0, 0.8)), to(rgba(0, 0, 0, 0)));
          mask-image: -webkit-gradient(linear, center top, right top, from(rgba(255, 0, 0, 0.8)), to(rgba(0, 0, 0, 0)));
}
.floatingArrow.right {
  right: 0;
  background: rgba(150, 150, 150, 0.8);
  -webkit-mask-image: -webkit-gradient(linear, center top, left top, from(rgba(255, 0, 0, 0.8)), to(rgba(0, 0, 0, 0)));
          mask-image: -webkit-gradient(linear, center top, left top, from(rgba(255, 0, 0, 0.8)), to(rgba(0, 0, 0, 0)));
}
.floatingArrow:hover {
  opacity: 1;
  -webkit-backdrop-filter: blur(1px);
          backdrop-filter: blur(1px);
}
.floatingArrow .arrow {
  border: 5px solid darkslategray;
  height: 100px;
  width: 100px;
  position: relative;
  top: 50%;
  transform: translateY(-50%) rotate(45deg);
}
.floatingArrow .arrow.left {
  left: 30px;
}
.floatingArrow .arrow.right {
  right: 100px;
}

